





























import loadMore from '../../components/load-more.vue'

export default {
	components: {
		loadMore
	},
	data() {
		return {
			list: [
				{},{},{},{},{},{}
			],
			loadingType: 0,
			contentText: {
				contentdown: this.local('loadingDown'),
				contentrefresh: this.local('loadingFresh'),
				contentnomore: this.local('loadingNomore')
			}
		};
	},
	onLoad() {
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news',
			method: 'GET',
			data: {},
			success: res => {
				console.log(res);
			},
			fail: () => {},
			complete: () => {}
		});
	},
	onPullDownRefresh() {
		uni.showToast({
			title: "需要下拉刷新",
			complete: (res) => {
				uni.stopPullDownRefresh();
			}
		});
	},
	onReachBottom() {
		if (this.loadingType != 0) {
			return;
		}
		this.loadingType = 1;
		for (let i = 0; i < 6; i++) {
			this.list.push({});
		}
		setTimeout(() => {
			if (this.list.length >= 18) {
				this.loadingType = 2;
				return;
			}
			this.loadingType = 0;
		}, 800);
	},
	methods: {
		clickHeart(item) {
			uni.showToast({
				title: "点击了喜欢功能"
			});
		},
		goProDetail(item) {
			uni.navigateTo({
				url: '../product/pro-detail'
			});
		}
	}
}



